@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@1,500&family=Shrikhand&display=swap");

:root {
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */
body {
  font-family: "Rubik";
  color: var(--secondary);
}
.App {
  max-width: 960px;
  margin: 0 auto;
}
.title h1 {
  font-family: "Shrikhand";
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}
.title h2,
.title p {
  text-align: center;
}
.title h2 {
  margin-top: 60px;
  font-size: 2.6rem;
}

.progress-bar {
  height: 5px;
  background: var(--primary);
  margin-top: 20px;
}
.booba {
  display: inline-block;
  border-radius: 0.12em;
  text-decoration: none;
  font-family: "Rubik", sans-serif;
  color: #9a44e0;
  text-align: center;
  transition: all 0.2s;
}
.booba:hover {
  color: #4e4e4e;
}
.pabloo {
  height: 403.2px;
  width: 302.4px;
}
